<template>
  <div>
    <!--1<c-c v=1 a=2 :cccc="1" />-->
    <div class="home-page">
      <div class="app-info">
        <b></b>
        <div>
          <img src="../assets/img/wechat.png" alt="">
          <div></div>
        </div>
        <div>
          <img src="../assets/img/alipay.png" alt="">
          <div @click="openAlipay"> >></div>
        </div>
        <div>
          <button class="download" @click="download">Scene4</button>
          <div class="last-version">{{versionInfo.versionName}}</div>
        </div>
      </div>
      <!--<div class="app-name">Toolbox  Scene</div>-->
      <div class="pictures" ref="phone">
        <div class="picture">
          <div class="camera"></div>
          <div class="voice"></div>
          <img src="../assets/img/Screenshot.jpg" alt="">
          <!--<button class="download"></button>-->
        </div>
        <!--<div class="picture"></div>
        <div class="picture"></div>
        <div class="picture"></div>
        <div class="picture"></div>-->
      </div>
      <div class="author">Design by Ski</div>
      <div class="version">Scene</div>
      <div class="get-history" @click="getHisotry"> </div>
    </div>

    <div class="app-desc">
      <div class=""></div>
      <div class="function-desc"><pre>{{versionInfo.message}}</pre></div>
        
      <br />
      <br />

      <div class=""></div>
      <div class="main-function">
        <span></span><span></span><span></span><span>SWAP</span><span>CPU</span>
      </div>
      <div class="function-desc">
        <span></span> Soc
        <br />
        Scene - Scene
      </div>
      <div class="function-desc">
        <span></span> BootRecovery
      </div>
      <div class="function-desc">
        <span></span> 
      </div>
      <div class="function-desc">
        <span>SWAP</span> 
      </div>
      <div class="function-desc">
        <span>CPU</span> 
      </div>
      
      <br />
      <br />
      <div class=""></div>
      <br />
      <div class="function-desc">
         >= Android M(6.0)
      </div>

      <br />
      <div class=""></div>
      <div class="app-list">
        <div v-for="(app, $index) in appRecommend" :key="$index" class="app-item" @click="downloadApp(app)">
          <div>{{app.appName}}</div>
          <div class="app-item-desc">{{app.desc}}</div>
        </div>
      </div>
      <!--<div class="function-desc"></div>-->
    </div>
  </div>
</template>
<script>
import CC from './CC.vue'
import axios from 'axios'

export default {
  data () {
    return {
      versionInfo: {
        "title": "Scene",
        "message": "",
        "versionName": "...",
        "versionCode":"",
        "downloadUrl":""
      },
      appRecommend: [
        {
          appName: 'Scene ',
          desc: 'Scene  - ~',
          downloadUrl: 'http://atools.oss-cn-shenzhen.aliyuncs.com/app1/Scene%20-%20%E9%AB%98%E7%BA%A7%E8%AE%BE%E5%AE%9A_1.0.7%20scene.apk'
        },
        {
          appName: ' ',
          desc: 'DPIScene ',
          downloadUrl: 'http://atools.oss-cn-shenzhen.aliyuncs.com/app1/vaddin_old.apk'
        },
        {
          appName: 'Ding Hook',
          desc: 'XposedWIFI',
          downloadUrl: 'http://atools.oss-cn-shenzhen.aliyuncs.com/app1/DingHook%201.0.1.apk'
        }
        /*
        {
          appName: '8.0.2 ',
          desc: '~',
          downloadUrl: 'http://atools.oss-cn-shenzhen.aliyuncs.com/app1/%E9%92%9B%E5%A4%87%E4%BB%BD_8.0.2.apk'
        },
        {
          appName: 'GLTools 4.0 ',
          desc: 'GPUROOTSystemMagisk',
          downloadUrl: 'http://atools.oss-cn-shenzhen.aliyuncs.com/app1/GLTools_4.00.apk'
        },
        {
          appName: 'Ansole 4.0',
          desc: '',
          downloadUrl: 'http://atools.oss-cn-shenzhen.aliyuncs.com/app1/Ansole_4.0-release.apk'
        },
        {
          appName: 'APktool+ 1.7.8_adv',
          desc: '',
          downloadUrl: 'http://atools.oss-cn-shenzhen.aliyuncs.com/app1/APktool%2B%E5%8A%A9%E6%89%8B_1.7.8_adv.apk'
        },
        {
          appName: 'ES_4.1.8.4.2.apk',
          desc: '',
          downloadUrl: 'http://atools.oss-cn-shenzhen.aliyuncs.com/app1/ES%E6%96%87%E4%BB%B6%E6%B5%8F%E8%A7%88%E5%99%A8_4.1.8.4.2.apk'
        }
        */
      ],
      orientation: {
        absolute: false,
        alpha: 0,
        beta: 90,
        gamma: 0
      },
      animation: {
        current: 0,
        to: 0,
        interval: -1
      }
    }
  },
  components: {
    CC
  },
  created () {
    axios.get(window.location.protocol + '//vtools.oss-cn-beijing.aliyuncs.com/vi/Scene4.json?dt=' + new Date().getTime()).then(r => {
      this.versionInfo = r.data
      if (window.KrScriptCore && this.versionInfo.versionCode) {
        const currentVersion = window.KrScriptCore.executeShell('echo $PACKAGE_VERSION_CODE')
        if (currentVersion) {
          const versionInt = parseInt(currentVersion)
          if (window.SceneUI && versionInt < this.versionInfo.versionCode) {
            window.SceneUI.showToast('')
          }
        }
      }
    }).catch(e => {
      alert('~')
    })
  },
  methods: {
    downloadApp (app) {
      window.open(app.downloadUrl)
    },
    openAlipay () {
      var ua = navigator.userAgent.toLowerCase()
      if (ua.indexOf('micromessenger') !== -1 || ua.indexOf('qq') !== -1) {
        alert('QQ//TIM')
      }
      window.location.href = (`alipayqr://platformapi/startapp?saId=10000007&clientVersion=3.7.0.0718&qrcode=${encodeURIComponent('HTTPS://QR.ALIPAY.COM/FKX05665KXCDCLC2YAEL0E')}%3F_s%3Dweb-other&_t=${new Date().getTime()}`)
    },
    getHisotry () {
      window.location.href = 'https://github.com/helloklf/vtools/releases'
    },
    download () {
      var ua = navigator.userAgent.toLowerCase()
      if (ua.indexOf('iphone') > -1 || ua.indexOf('windows phone') > -1 || ua.indexOf('windows mobile') > -1) {
        alert('iOSWP')
        return
      }
      if (this.versionInfo.downloadUrl) {
        window.location.href = this.versionInfo.downloadUrl
      } else if (this.versionInfo.versionCode) {
        window.location.href = 'http://vtools.oss-cn-beijing.aliyuncs.com/app-release' + this.versionInfo.versionCode + '.apk'
      } else {
        window.location.href = 'http://vtools.oss-cn-beijing.aliyuncs.com/app-release.apk'
      }
    },
    deviceOrientation (e) {
      if (this.$refs.phone) {
        // this.orientation.absolute = e.absolute
        this.orientation.alpha = parseInt(e.alpha)
        this.orientation.beta = parseInt(e.beta) - 45
        this.orientation.gamma = parseInt(e.gamma)

        //this.$refs.phone.setAttribute('style', `transform: rotateY(${this.orientation.alpha}deg) rotateX(${this.orientation.beta}deg) rotateZ(${this.orientation.gamma}deg);`)
        //this.$refs.phone.style.transform = `rotateY(${this.orientation.alpha}deg) rotateX(${this.orientation.beta}deg) rotateZ(${this.orientation.gamma}deg)`
        // this.animationTo(this.orientation.gamma * 3 - 150)
        this.$refs.phone.style.transform = (`translateX(${this.orientation.gamma * 3 - 30}px)`)
      }
    },
    animationTo (to) {
      this.animation.to = to
      if (this.animation.interval < 0) {
        this.animation.interval = window.setInterval(() => {
          let v = Math.abs(this.animation.current - this.animation.to)
          let sqrt = parseInt(Math.sqrt(v, 3)) / 2
          if (v <= sqrt) {
            window.clearInterval(this.animation.interval)
            this.animation.interval = -1
            this.animation.current = this.animation.to
          } else if (this.animation.to > this.animation.current) {
            this.animation.current += sqrt
          } else {
            this.animation.current -= sqrt
          }
          this.$refs.phone.style.transform = (`translateX(${this.animation.current}px)`)
        }, 4)
      }
    }
  },
  mounted () {
    window.addEventListener('deviceorientation', this.deviceOrientation)
  },
  computed: {
  }
}
</script>

<style lang="scss">
@keyframes phone {
  0% {
    transform: translateX(-50vw) scale(1.06);
  }
  30% {
