    box-sizing: border-box;
    .camera {
      height: 10px;
      width: 10px;
      border-radius: 50%;
      position: absolute;
      right: 14px;
      bottom: -26px;
      border: 1px solid #877;
      background: #000;
    }
    .voice {
      height: 3px;
      width: 70px;
      background-color: #888;
      position: absolute;
      right: auto;
      left: 50%;
      margin: auto auto auto -35px;
      border-radius: 0px 0px 20px 20px;
      top: -8px;
    }
    img {
      width: 100%;
      height: 100%;
      border-radius: 1.2em;
      z-index: -1;
      border: 1px solid #ddd;
      box-shadow: 0 0 3px #000 inset;
    }
  }
  .picture:active {
  }
  .picture:nth-child(1) {
    z-index: 5;
  }
  .picture:nth-child(2) {
    z-index: 4;
    transform: translateX(30px) scale(0.9);
  }
  .picture:nth-child(3) {
    z-index: 3;
    transform: translateX(-30px) scale(0.9);
  }
  .picture:nth-child(4) {
    z-index: 2;
    transform: translateX(60px) scale(0.8);
  }
  .picture:nth-child(5) {
    z-index: 2;
    transform: translateX(-60px) scale(0.8);
  }
  .app-name {
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.5);
    text-align: center;
    margin-bottom: 10px;
    margin-top: 10px;
    font-size: 28px;
  }
  .author {
    text-align: center;
    margin-bottom: 10px;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.4);
  }
  .download {
    background-color: #138ed6;
    color: #fff;
    display: block;
    width: 100%;
    height: 12vw;
    font-size: 4vw;
    display: inline-block;
    margin: 1em auto 0.1em auto;
    border-radius: 10px;
    letter-spacing: 2px;
    font-weight: 100;
    border: none;
    transition: all 0.2s;
    box-shadow: 1px 1px 20px #888;
    box-shadow: none;
  }
  .download:active {
    transform: scale(0.95);
  }
  .last-version {
    font-size: 0.8em;
    color: #888;
  }
  .version {
    text-align: center;
    font-size: 12px;
    margin-bottom: 5px;
  }
  .get-history {
    text-align: center;
    margin-bottom: 1em;
    font-size: 12px;
    opacity: 0.6;
  }
}
.app-desc {
  padding: 20vw 10vw 10vw 10vw;
  .main-function {
    font-size: 12px;
    margin: 10px 0;
  }
  .main-function span {
