  }
  .function-desc {
    font-size: 12px;
    color: rgba(255,255,255, 0.9);
    margin-bottom: 10px;
    span {
      color: #fff;
    }
    span::after {
      content: " : ";
    }
  }
}
.app-list {
  margin: 10px 0;
  .app-item {
    font-size: 13px;
    background-color: rgba(255, 255,255, 0.3);
    border-radius: 5px;
    padding: 6px 10px;
    margin-bottom: 4px;
  }
  .app-item-desc {
    font-weight: 100;
