<template>
  <UpdatePage></UpdatePage>
</template>

<script>
import UpdatePage from "./UpdatePage"

export default {
  components: {
    UpdatePage
  },
  created () {
    if (window.SceneUI) {
      window.SceneUI.setStatusBarColor('#ff00b9c2')
      window.SceneUI.setNavigationBarColor('#ff00b9c2')
    }
  },
  computed: {
    inScene() {
      return (window.KrScriptCore && true);
    }
  }
};
</script>